<template>
    <div class="player">
      <div id="player"></div>
    </div>
</template>

<script>
export default {
  name:"Player",
  data () {
    return {

    }
  },
  methods: {
      check_login(){
        // 
        let token = localStorage.user_token || sessionStorage.user_token;
        if( !token ){
          this.$alert("").then(()=>{
            this.$router.push("/login");
          });
          return false; // 
        }
        return token;
      },
      init_player(){
          // html5
          // 
          let token = this.check_login();
          let user_name = localStorage.user_name || sessionStorage.user_name;
          let _this = this;
          // let vid = this.$route.query.vid;
          let vid = 'a971622b88114705e77d86b6d3162916_a';
          if(!vid){
              this.$message('');
              return false
          }
          var player = polyvObject('#player').videoPlayer({
              wrap: '#player',
              width: document.documentElement.clientWidth-260, // 
              height: document.documentElement.clientHeight, // 
              forceH5: true,
              vid: vid,
              code: user_name, // 
              // 
              playsafe: function (vid, next) { // token
                  _this.$axios.get(`${_this.$settings.Host}/course/polyv/token/`,{
                    params:{
                      vid: vid,
                    },
                    headers:{
                      "Authorization":"jwt " + token,
                    }
                  }).then(function (response) {
                      console.log(response);
                      next(response.data.token);
                  })

              }
          });
      }
  },
  mounted(){
    this.init_player();
  },
  computed: {
  }

}
</script>

<style scoped>
</style>



