<template>
    <div class="detail">
      <Header/>
      <div class="main">
        <div class="course-info">
          <div class="wrap-left">
            <videoPlayer v-if="course.course_video" class="video-player vjs-custom-skin"
              ref="videoPlayer"
              :playsinline="true"
              :options="playerOptions"
              @play="onPlayerPlay($event)"
              @pause="onPlayerPause($event)"
            >
            </videoPlayer>
            <img v-if="!course.course_video" src="course.course_img" alt="">
          </div>
          <div class="wrap-right">
            <h3 class="course-name">{{course.name}}</h3>
            <p class="data">{{course.students}}&nbsp;&nbsp;&nbsp;&nbsp;{{course.lessons}}/{{course.lessons==course.pub_lessons?'':`${course.pub_lessons}`}}&nbsp;&nbsp;&nbsp;&nbsp;{{course.level_name}}</p>
            <div class="sale-time" v-if="course.active_time>0">
              <p class="sale-type">{{course.discount_name}}</p>
              <p class="expire"> {{keepformat(course.active_time / 86400)}} {{keepformat((course.active_time / 3600) % 24)}} {{keepformat((course.active_time / 60) % 60)}} <span class="second">{{keepformat(course.active_time % 60)}}</span> </p>
            </div>
            <p class="course-price" v-if="course.active_time>0">
              <span></span>
              <span class="discount">{{course.real_price}}</span>
              <span class="original">{{course.price}}</span>
            </p>
            <div class="sale-time course-price2" v-if="course.active_time<1">
              <span></span>
              <p class="sale-type discount" style="color:#fff">{{course.price}}</p>
            </div>
            <div class="buy">
              <div class="buy-btn">
                <button class="buy-now"></button>
                <button class="free"></button>
              </div>
              <div class="add-cart" @click="addCartHander"><img src="/static/image/cart-yellow.svg" alt=""></div>
            </div>
          </div>
        </div>
        <div class="course-tab">
          <ul class="tab-list">
            <li :class="tabIndex==1?'active':''" @click="tabIndex=1"></li>
            <li :class="tabIndex==2?'active':''" @click="tabIndex=2"> <span :class="tabIndex!=2?'free':''">()</span></li>
            <li :class="tabIndex==3?'active':''" @click="tabIndex=3"> (42)</li>
            <li :class="tabIndex==4?'active':''" @click="tabIndex=4"></li>
          </ul>
        </div>
        <div class="course-content">
          <div class="course-tab-list">
            <div class="tab-item" v-if="tabIndex==1">
              <div class="brief_box" v-html="course.real_brief"></div>
            </div>
            <div class="tab-item" v-if="tabIndex==2">
              <div class="tab-item-title">
                <p class="chapter"></p>
                <p class="chapter-length">{{course.chapter_list.length}} {{course.lessons}}</p>
              </div>
              <div class="chapter-item" v-for="chapter,key in course.chapter_list" :key="key">
                <p class="chapter-title"><img src="/static/image/1.svg" alt="">{{chapter.chapter}}{{chapter.name}}</p>
                <ul class="lesson-list">
                  <li class="lesson-item" v-for="lesson,key in chapter.lesson_list" :key="key">
                    <p class="name"><span class="index">{{chapter.chapter}}-{{key+1}}</span><span class="free" v-if="lesson.free_trail"></span></p>
                    <p class="time">{{lesson.duration}} <img src="/static/image/chapter-player.svg"></p>
                    <button class="try" v-if="lesson.free_trail && lesson.section_type==2"><router-link :to="`/course/lesson/video/${lesson.id}/`"></router-link></button>
                    <button class="try" v-if="lesson.free_trail && lesson.section_type==1"><router-link :to="`/course/lesson/doc/${lesson.id}/`"></router-link></button>
                    <button class="try" v-if="lesson.free_trail && lesson.section_type==0"><router-link :to="`/course/lesson/exam/${lesson.id}/`"></router-link></button>
                    <button class="try" v-if="!lesson.free_trail"></button>
                  </li>
                </ul>
              </div>
            </div>
            <div class="tab-item" v-if="tabIndex==3">
              
            </div>
            <div class="tab-item" v-if="tabIndex==4">
              
            </div>
          </div>
          <div class="course-side">
             <div class="teacher-info">
               <h4 class="side-title"><span></span></h4>
               <div class="teacher-content">
                 <div class="cont1">
                   <img src="/static/image/8268683.png">
                   <div class="name">
                     <p class="teacher-name">{{course.teacher.name}}</p>
                     <p class="teacher-title">{{course.teacher.title}}</p>
                   </div>
                 </div>
                 <p class="narrative" >{{course.teacher.brief}}</p>
               </div>
             </div>
          </div>
        </div>
      </div>
      <Footer/>
    </div>
</template>

<script>
import Header from "./common/Header"
import Footer from "./common/Footer"
// 
import {videoPlayer} from 'vue-video-player'

export default {
    name: "Detail",
    data(){
      return {
        course: {
          // 'id': 0, // id
        },

        tabIndex:2, // 
        playerOptions:{
            playbackRates: [0.7, 1.0, 1.5, 2.0], // 
            autoplay: false, //true,
            muted: false, // 
            loop: false, // 
            preload: 'auto',  // <video>auto,
            language: 'zh-CN',
            aspectRatio: '16:9', //  - "16:9""4:3"
            fluid: true, // trueVideo.js player
            sources: [{ // 
              type: "video/mp4",
              src: ""
              // src: "http://img.ksbbs.com/asset/Mon_1703/05cacb4e02f9d9e.mp4" //
            }],
            poster: "../../static/image/course-cover.jpeg", //
            width: document.documentElement.clientWidth, // 
            notSupportedMessage: '', //Video.js
        }
      }
    },
    components:{
        Header,
        Footer,
        videoPlayer,
    },
    created(){
        // newrouterrouteroutes
        // 
        this.course.id = this.$route.params.course;
        // 
        this.get_course();
    },
    methods: {
        onPlayerPlay(event){
            // 
            // alert('')
        },
        onPlayerPause(event){
            // 
            // alert('')
        },
        get_course(){
            // 
            this.$axios.get(`${this.$settings.Host}/course/${this.course.id}`).then(response=>{
                this.course = response.data;
                console.log(this.course);
                // ***
                this.playerOptions.poster = response.data.course_img;
                // ***
                this.playerOptions.sources[0].src = response.data.course_video;
                // ***
                let timer = setInterval(()=>{
                    if (this.course.active_time > 0){
                        this.course.active_time --;
                    }else{
                        clearInterval(timer);
                    }
                },1000)
            }).catch(error=>{
                console.log(error);
                // this.$router.go(-1);
            })
        },
        addCartHander(){
            // 
            // 
            let user_token = localStorage.user_token || sessionStorage.user_token;
            if(!user_token){
                this.$confirm('', '').then(response=>{
                        this.$router.push('/login');
                    })
            }

            // ajaxpost
            this.$axios.post(`${this.$settings.Host}/cart/course/add_course/`, {
                course_id: this.course.id,
            },{
                // headers jwt
                headers: {
                    'Authorization': 'jwt ' + user_token
                }
            }).then(response=>{
                // 
                // localStorage.user_total = response.data.total;
                this.$store.commit('get_total', response.data.total);
                this.$message('')
            }).catch(error=>{
                console.log(error.response.data)
            })
        },
        keepformat(timer){
            let timestamp = parseInt(timer);
            if (timestamp>=10){
                return timestamp
            }else{
                return '0'+timestamp
            }
        }

    },

}
</script>

<style scoped>
.main{
  background: #fff;
  padding-top: 30px;
}
.course-info{
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.wrap-left{
  float: left;
  width: 690px;
  height: 388px;
  background-color: #000;
}
.wrap-right{
  float: left;
  position: relative;
  height: 388px;
}
.course-name{
  font-size: 20px;
  color: #333;
  padding: 10px 23px;
  letter-spacing: .45px;
}
.data{
  padding-left: 23px;
  padding-right: 23px;
  padding-bottom: 16px;
  font-size: 14px;
  color: #9b9b9b;
}
.sale-time{
  width: 464px;
  background: #fa6240;
  font-size: 14px;
  color: #4a4a4a;
  padding: 10px 23px;
  overflow: hidden;
}
.sale-type {
  font-size: 16px;
  color: #fff;
  letter-spacing: .36px;
  float: left;
}
.sale-time .expire{
  font-size: 14px;
  color: #fff;
  float: right;
}
.sale-time .expire .second{
  width: 24px;
  display: inline-block;
  background: #fafafa;
  color: #5e5e5e;
  padding: 6px 0;
  text-align: center;
}
.course-price{
  background: #fff;
  font-size: 14px;
  color: #4a4a4a;
  padding: 5px 23px;
}
.discount{
  font-size: 26px;
  color: #fa6240;
  margin-left: 10px;
  display: inline-block;
  margin-bottom: 4px;
}
.original{
  font-size: 14px;
  color: #9b9b9b;
  margin-left: 10px;
  text-decoration: line-through;
}
.buy{
  width: 464px;
  padding: 0px 23px;
  position: absolute;
  left: 0;
  bottom: 20px;
  overflow: hidden;
}
.buy .buy-btn{
  float: left;
}
.buy .buy-now{
  width: 125px;
  height: 40px;
  border: 0;
  background: #ffc210;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  margin-right: 15px;
  outline: none;
}
.buy .free{
  width: 125px;
  height: 40px;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 15px;
  background: #fff;
  color: #ffc210;
  border: 1px solid #ffc210;
}
.add-cart{
  float: right;
  font-size: 14px;
  color: #ffc210;
  text-align: center;
  cursor: pointer;
  margin-top: 10px;
}
.add-cart img{
  width: 20px;
  height: 18px;
  margin-right: 7px;
  vertical-align: middle;
}

.course-tab{
    width: 100%;
    background: #fff;
    margin-bottom: 30px;
    box-shadow: 0 2px 4px 0 #f0f0f0;

}
.course-tab .tab-list{
    width: 1200px;
    margin: auto;
    color: #4a4a4a;
    overflow: hidden;
}
.tab-list li{
    float: left;
    margin-right: 15px;
    padding: 26px 20px 16px;
    font-size: 17px;
    cursor: pointer;
}
.tab-list .active{
    color: #ffc210;
    border-bottom: 2px solid #ffc210;
}
.tab-list .free{
    color: #fb7c55;
}
.course-content{
    width: 1200px;
    margin: 0 auto;
    background: #FAFAFA;
    overflow: hidden;
    padding-bottom: 40px;
}
.course-tab-list{
    width: 880px;
    height: auto;
    padding: 20px;
    background: #fff;
    float: left;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
    box-shadow: 0 2px 4px 0 #f0f0f0;
}
.tab-item{
    width: 880px;
    background: #fff;
    padding-bottom: 20px;
    box-shadow: 0 2px 4px 0 #f0f0f0;
}
.tab-item-title{
    justify-content: space-between;
    padding: 25px 20px 11px;
    border-radius: 4px;
    margin-bottom: 20px;
    border-bottom: 1px solid #333;
    border-bottom-color: rgba(51,51,51,.05);
    overflow: hidden;
}
.chapter{
    font-size: 17px;
    color: #4a4a4a;
    float: left;
}
.chapter-length{
    float: right;
    font-size: 14px;
    color: #9b9b9b;
    letter-spacing: .19px;
}
.chapter-title{
    font-size: 16px;
    color: #4a4a4a;
    letter-spacing: .26px;
    padding: 12px;
    background: #eee;
    border-radius: 2px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}
.chapter-title img{
    width: 18px;
    height: 18px;
    margin-right: 7px;
    vertical-align: middle;
}
.lesson-list{
    padding:0 20px;
}
.lesson-list .lesson-item{
    padding: 15px 20px 15px 36px;
    cursor: pointer;
    justify-content: space-between;
    position: relative;
    overflow: hidden;
}
.lesson-item .name{
    font-size: 14px;
    color: #666;
    float: left;
}
.lesson-item .index{
    margin-right: 5px;
}
.lesson-item .free{
    font-size: 12px;
    color: #fff;
    letter-spacing: .19px;
    background: #ffc210;
    border-radius: 100px;
    padding: 1px 9px;
    margin-left: 10px;
}
.lesson-item .time{
    font-size: 14px;
    color: #666;
    letter-spacing: .23px;
    opacity: 1;
    transition: all .15s ease-in-out;
    float: right;
}
.lesson-item .time img{
    width: 18px;
    height: 18px;
    margin-left: 15px;
    vertical-align: text-bottom;
}
.lesson-item .try{
    width: 86px;
    height: 28px;
    background: #ffc210;
    border-radius: 4px;
    font-size: 14px;
    color: #fff;
    position: absolute;
    right: 20px;
    top: 10px;
    opacity: 0;
    transition: all .2s ease-in-out;
    cursor: pointer;
    outline: none;
    border: none;
}
.lesson-item:hover{
    background: #fcf7ef;
    box-shadow: 0 0 0 0 #f3f3f3;
}
.lesson-item:hover .name{
    color: #333;
}
.lesson-item:hover .try{
    opacity: 1;
}

.course-side{
    width: 300px;
    height: auto;
    margin-left: 20px;
    float: right;
}
.teacher-info{
    background: #fff;
    margin-bottom: 20px;
    box-shadow: 0 2px 4px 0 #f0f0f0;
}
.side-title{
    font-weight: normal;
    font-size: 17px;
    color: #4a4a4a;
    padding: 18px 14px;
    border-bottom: 1px solid #333;
    border-bottom-color: rgba(51,51,51,.05);
}
.side-title span{
    display: inline-block;
    border-left: 2px solid #ffc210;
    padding-left: 12px;
}

.teacher-content{
    padding: 30px 20px;
    box-sizing: border-box;
}

.teacher-content .cont1{
    margin-bottom: 12px;
    overflow: hidden;
}

.teacher-content .cont1 img{
    width: 54px;
    height: 54px;
    margin-right: 12px;
    float: left;
}
.teacher-content .cont1 .name{
    float: right;
}
.teacher-content .cont1 .teacher-name{
    width: 188px;
    font-size: 16px;
    color: #4a4a4a;
    padding-bottom: 4px;
}
.teacher-content .cont1 .teacher-title{
    width: 188px;
    font-size: 13px;
    color: #9b9b9b;
    white-space: nowrap;
}
.teacher-content .narrative{
    font-size: 14px;
    color: #666;
    line-height: 24px;
}
  .brief_box image{
    max-width: 100%;
  }
  .try a{
    color: #fff;
  }
  .course-price2 span{
    float: left;
    margin-top: 10px;
    color: #fff;
  }
</style>



